<template>
    <div class="order">
        <Header title="订单" />
        <div class="content">
            <van-tabs color="#ffc400">
                <van-tab v-for="(i, index) in navData" :title="i" :key="index">
                    <div
                    v-for="(i, index) in store.state.orderListed" :key="index"
                    v-if="i === '全部' && store.state.orderListed.length"
                    >
                        <van-card :num="i.num" :price="i.price" :title="i.title">
                        </van-card>
                    </div>
                    <Blank v-else />
                </van-tab>
            </van-tabs>
        </div>
        <Footer />
    </div>
</template>
<script>
import Footer from "../../components/Footer"
import Header from "@/components/Header.vue";
import Blank from "../myCart/components/Blank.vue"
import {reactive,toRefs} from "vue"
import { useStore } from "vuex";
export default {
    components: {
        Footer,
        Header,
        Blank
    },
    setup(){
        const store=useStore()
        let data=reactive({
            navData:["全部","交易完成","待付款","待发货","已发货"]
        })

        return {
            ...toRefs(data),
            store
        }
    }
}
</script>
<style scoped lang="less">
.order{
    display: flex;
    flex-direction: column;
    height: 100%;
    .content{
        flex: 1;
        overflow-y: auto;
    }
}
</style>